<template>
    <div class="page-invoice-list">
        <i class="icon icon-abs" @click="flush">autorenew</i>
        <div class="flex-v-center tool-bar">
            <div class="flex-v-center search-bar" style="margin-right: 20px;">
                <i class="icon f-20 c-8">search</i>
                <input type="text" class="flex-item" v-model="filter.orderNo" @change="
            $refs.list.update(true);
            getPriceTotal();
          " placeholder="" style="width: 200px;">
                <i class="icon f-20 c-8">search</i>
                <input type="text" class="flex-item" v-model="filter.constractParty" @change="
            $refs.list.update(true);
            getPriceTotal();
          " placeholder="" style="width: 200px;">
                
                <select v-model="filter.status" class="f-14" @change="
            $refs.list.update(true);
            getPriceTotal();
          ">
                    <option v-for="item in statusList" :value="item.status" v-bind:key="item.id">{{ item.name }}</option>
                </select>
                <i class="icon" style="margin-left: -20px;pointer-events:none;">arrow_drop_down</i>
            </div>
            <div class="flex-item" style="text-align:right;color:#0f95ff">():{{ priceTotal }}</div>
            <!-- <router-link to="/jgzx-invoice/add">
      <btn class="b" flat color="#008eff"></btn>
            </router-link>-->
        </div>
        <div class="flex-item scroll-y">
            <data-list ref="list" :page-size="15" :param="filter" url="/haolifa/invoice/list/0" method="post">
                <tr slot="header">
                    <th style="width: 60px;"></th>
                    <th></th>
                    <th></th>
                    <th></th>
                    <th></th>
                    <th></th>
                    <th></th>
                    <th></th>
                    <th></th>
                    <!-- <th class="t-right" style="width: 80px;"></th> -->
                </tr>
                <!-- item: ; index:  -->
                <template slot="item" slot-scope="{ item, index }">
                    <td class="c-a">{{ index }}</td>
                    <td>{{ item.orderNo }}</td>
                    <td>{{ item.constractParty }}</td>
                    <td>{{ item.orderAmount }}</td>
                    <td>{{ item.totalAmount }}</td>
                    <td>{{ statusArr[item.status].name }}</td>
                    <td>{{ item.remark }}</td>
                    <td>{{ item.createTime }}</td>
                    <td>{{ item.updateTime }}</td>
                    <!-- <td class="t-right">
                        <a href="javascript:;" v-if="item.status == 1" class="blue" @click="remove(item)"></a>
                    </td>-->
                </template>
            </data-list>
        </div>
    </div>
</template>

<script>
import DataList from "@/components/datalist";
export default {
    name: "invoice-list",
    components: { DataList },
    data() {
        return {
            priceTotal: "",
            statusList: [
                { status: -1, name: "" },
                { status: 0, name: "" },
                { status: 1, name: "" },
                { status: 2, name: "" }
            ],
            statusArr: [
                { status: 0, name: "" },
                { status: 1, name: "" },
                { status: 2, name: "" }
            ],
            filter: {
                type: 1,
                status: -1,
                orderNo: "",
                constractParty: ""
            }
        };
    },
    mounted() {
        this.getPriceTotal();
    },
    methods: {
        flush() {
            this.filter = {
                type: 1,
                status: -1,
                orderNo: "",
                constractParty: ""
            };
            this.$refs.list.update(true);
            this.getPriceTotal();
        },
        edit(item) {
            this.$router.push(`/jgzx-invoice/edit?id=${item.id}`);
        },
        getPriceTotal() {
            this.$http
                .post(`/haolifa/statistics/money/invoice/1`, this.filter)
                .then(res => {
                    this.priceTotal = res.totalAmount;
                })
                .catch(e => {
                    this.$toast(e.msg || e.message);
                });
        },
        remove(item) {
            this.$confirm({
                title: "",
                text: `<br><b>${item.invoiceNo}</b>`,
                color: "red",
                btns: ["", ""],
                yes: () => {
                    this.$http
                        .get(`/haolifa/invoice/delete/${item.id}`)
                        .then(res => {
                            this.$toast("");
                            this.$refs.list.update(true);
                        })
                        .catch(e => {
                            this.$toast(e.msg || e.message);
                        });
                }
            });
        }
    }
};
</script>

