<template>
    <div class="page-invoice-list">
        <i class="icon icon-abs" @click="flush">autorenew</i>
        <div class="flex-v-center tool-bar">
            <div class="flex-v-center search-bar" style="margin-right: 20px;">
                <i class="icon f-20 c-8">search</i>
                <input type="text" class="flex-item" v-model="filter.projectName" @change="
            $refs.list.update(true)" placeholder="" style="width: 200px;">
            </div>
            <div class="flex-item"></div>
            <btn class="b" flat color="#008eff" @click="add"></btn>
        </div>
        <div class="flex-item scroll-y">
            <data-list ref="list" :page-size="15" :param="filter" url="/haolifa/pay-assessment-quota/getList" method="post">
                <tr slot="header">
                    <th style="width: 60px;"></th>
                    <th></th>
                    <th></th>
                    <th></th>
                    <th></th>
                    <th></th>
                    <th></th>
                    <th></th>
                    <th></th>
                    <th></th>
                    <th class="t-right" style="width: 80px;"></th>
                </tr>
                <!-- item: ; index:  -->
                <template slot="item" slot-scope="{ item, index }">
                    <td class="c-a">{{ index }}</td>
                    <td>{{ item.projectName }}</td>
                    <td>{{ item.departName }}</td>
                    <td>{{ item.quotaName }}</td>
                    <td>{{ item.quotaContent }}</td>
                    <td>{{ item.score }}</td>
                    <td>{{ item.standard }}</td>
                    <td>{{ item.remark }}</td>
                    <td>{{ item.createTime }}</td>
                    <td>{{ item.updateTime }}</td>
                    <td class="t-right">
                        <a href="javascript:;" class="blue" @click="edit(item)"> |&nbsp;</a>
                        <a href="javascript:;" class="red" @click="remove(item)"></a>
                    </td>
                </template>
            </data-list>
        </div>
        <layer v-if="layer" :title="form.id ?'':'' " width="70%">
            <div class="layer-text" style="padding-bottom: 50px;">
                <div class="flex">
                    <input-box v-model="form.projectName" class="flex-item mr-20" hint="" label=""></input-box>
                    <input-box v-model="form.quotaName" class="flex-item mr-20" hint="" label=""></input-box>
                </div>
                <div class="flex">
                    <select-box v-model="form.departId" class="flex-item mr-20" hint="" :list="postList" label=""></select-box>
                    <input-box v-model="form.score" type="number" class="flex-item mr-20" label=""></input-box>
                </div>
                <div class="flex">
                    <input-box v-model="form.quotaContent" class="flex-item mr-20" label=""></input-box>
                </div>
                <div class="flex">
                    <input-box v-model="form.standard" class="flex-item mr-20" label=""></input-box>
                </div>
                <div class="flex">
                    <input-box v-model="form.remark" class="flex-item mr-20" label=""></input-box>
                </div>
            </div>
            <div class="layer-btns">
                <el-button class="mr-20" size="mini" :loading="loading" type="primary" @click="save"></el-button>
                <el-button class="mr-20" size="mini" @click="close"></el-button>
            </div>
        </layer>
    </div>
</template>

<script>
import DataList from "@/components/datalist";
export default {
    name: "khzb-list",
    components: { DataList },
    data() {
        return {
            filter: {
                projectName: ""
            },
            layer: false,
            form: {
                id: "",
                projectName: "",
                quotaContent: "",
                quotaName: "",
                remark: "",
                score: 0,
                standard: "",
                departName: "",
                departId: ""
            },
            postList: [],
            loading: false
        };
    },
    mounted() {
        this.getPostList();
    },
    methods: {
        flush() {
            this.filter = {
                projectName: ""
            };
            this.$refs.list.update(true);
        },
        edit(item) {
            this.layer = true;
            this.form.id = item.id;
            this.form.projectName = item.projectName;
            this.form.quotaContent = item.quotaContent;
            this.form.quotaName = item.quotaName;
            this.form.remark = item.remark;
            this.form.score = item.score;
            this.form.standard = item.standard;
        },
        remove(item) {
            this.$confirm({
                title: "",
                text: `<br><b>${item.projectName}</b>`,
                color: "red",
                btns: ["", ""],
                yes: () => {
                    this.$http
                        .get(`/haolifa/pay-assessment-quota/del/${item.id}`)
                        .then(res => {
                            this.$toast("");
                            this.$refs.list.update(true);
                        })
                        .catch(e => {
                            this.$toast(e.msg || e.message);
                        });
                }
            });
        },
        getPostList() {
            this.$http
                .post("/haolifa/pay-production-work-shop/getAllList")
                .then(res => {
                    res.map(item => {
                        this.postList.push({
                            text: item.postName,
                            value: item.id
                        });
                    });
                })
                .catch(e => {
                    this.$toast(e.msg || e.message);
                });
        },
        add() {
            this.layer = true;
        },
        save() {
            if (
                !this.form.projectName.trim() ||
                !this.form.departId ||
                !this.form.quotaName.trim()
            ) {
                this.$toast("");
                return;
            }
            this.postList.map(item => {
                if (item.value == this.form.departId) {
                    this.form.departName = item.text;
                }
            });
            this.loading = true;
            let url = this.form.id
                ? "/haolifa/pay-assessment-quota/edit"
                : "/haolifa/pay-assessment-quota/save";
            this.$http
                .post(url, this.form)
                .then(res => {
                    this.close();
                    this.$toast("");
                    this.$refs.list.update(true);
                    this.loading = false;
                })
                .catch(e => {
                    this.loading = false;
                    this.$toast(e.msg || e.message);
                });
        },
        close() {
            this.layer = false;
            this.form = {
                id: "",
                projectName: "",
                quotaContent: "",
                quotaName: "",
                remark: "",
                score: 0,
                standard: ""
            };
        }
    }
};
</script>

<style lang="less">
.page-invoice-list {
    select {
        background: none;
        border: none;
        outline: none;
        padding: 5px 20px 5px 10px;
        appearance: none;
    }
    .scroll-y {
        padding-bottom: 40px;
    }

    //
}
.fixed-length {
    width: 100px;
    display: block;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
}
</style>
