<template>
    <div class="page-invoice-list">
        <i class="icon icon-abs" @click="flush">autorenew</i>
        <div class="flex-v-center tool-bar">
            <div class="flex-v-center search-bar" style="margin-right: 20px;">
                <i class="icon f-20 c-8">search</i>
                <input type="text" class="flex-item" v-model="filter.workshopName" @change="
                $refs.list.update(true)" placeholder="" style="width: 180px;">
                <input type="text" class="flex-item" v-model="filter.workType" @change="
                $refs.list.update(true)" placeholder="" style="width: 180px;">
                <input type="text" class="flex-item" v-model="filter.procedureName" @change="
                $refs.list.update(true)" placeholder="" style="width: 180px;">
                <input type="text" class="flex-item" v-model="filter.appSpecifications" @change="
                $refs.list.update(true)" placeholder="" style="width: 180px;">
                <input type="text" class="flex-item" v-model="filter.appModel" @change="
                $refs.list.update(true)" placeholder="" style="width: 180px;">
                <input type="text" class="flex-item" v-model="filter.idCategory" @change="
                $refs.list.update(true)" placeholder="" style="width: 180px;">
                <input type="text" class="flex-item" v-model="filter.hourQuotaPrice" @change="
                $refs.list.update(true)" placeholder="" style="width: 180px;">
            </div>
            <div class="flex-item"></div>
            <btn class="b" flat color="#008eff" @click="add"></btn>
        </div>
        <div class="flex-item scroll-y">
            <data-list ref="list" :page-size="15" :param="filter" url="/haolifa/pay-hour_quota/getList" method="post">
                <tr slot="header">
                    <th style="width: 60px;"></th>
                    <th></th>
                    <th></th>
                    <th></th>
                    <th></th>
                    <th></th>
                    <th></th>
                    <th></th>
                    <th></th>
                    <th></th>
                    <th></th>
                    <th></th>
                    <th class="t-right" style="width: 80px;"></th>
                </tr>
                <!-- item: ; index:  -->
                <template slot="item" slot-scope="{ item, index }">
                    <td class="c-a">{{ index }}</td>
                    <td>{{ item.workshopName }}</td>
                    <td>{{ item.workType }}</td>
                    <td>{{ item.productName }}</td>
                    <td>{{ item.procedureName }}</td>
                    <td>{{ item.postCode }}</td>
                    <td>{{ item.appSpecifications }}</td>
                    <td>{{ item.appModel }}</td>
                    <td>{{ item.idCategory }}</td>
                    <td>{{ item.hourQuotaPrice }}</td>
                    <td>{{ item.createTime }}</td>
                    <td>{{ item.updateTime }}</td>
                    <td class="t-right">
                        <a href="javascript:;" class="blue" @click="edit(item)"> |&nbsp;</a>
                        <a href="javascript:;" class="red" @click="remove(item)"></a>
                    </td>
                </template>
            </data-list>
        </div>
        <layer v-if="layer" :title="form.id ?'':'' " width="70%">
            <div class="layer-text" style="padding-bottom: 50px;">
                <div class="flex">
                    <select-box v-model="form.workshopName" class="flex-item mr-20" hint="" :list="deptList" label="" @change="getProcedureList"></select-box>
                    <select-box v-model="form.procedureName" class="flex-item mr-20" hint="" :list="procedureNameList" label="" @change="procedureNameChange"></select-box>
                    <input-box v-model="form.postCode" class="flex-item mr-20" label="" :disabled="true"></input-box>
                </div>
                <div class="flex">
                    <input-box v-model="form.workType" class="flex-item mr-20" label=""></input-box>
                    <input-box v-model="form.productName" class="flex-item mr-20" label=""></input-box>
                    <input-box v-model="form.appSpecifications" class="flex-item mr-20" label=""></input-box>
                </div>
                <div class="flex">
                    <input-box v-model="form.appModel" class="flex-item mr-20" label=""></input-box>
                    <input-box v-model="form.idCategory" class="flex-item mr-20" label=""></input-box>
                    <input-box v-model="form.hourQuotaPrice" type="number" class="flex-item mr-20" label=""></input-box>
                </div>
                <div class="flex"></div>
            </div>
            <div class="layer-btns">
                <el-button class="mr-20" size="mini" :loading="loading" type="primary" @click="save"></el-button>
                <el-button class="mr-20" size="mini" @click="close"></el-button>
            </div>
        </layer>
    </div>
</template>

<script>
import DataList from "@/components/datalist";
export default {
    name: "jiaoxiaogongxu-list",
    components: { DataList },
    data() {
        return {
            filter: {
                appModel: "",
                appSpecifications: "",
                hourQuotaPrice: "",
                id: "",
                idCategory: "",
                postCode: "",
                procedureName: "",
                productName: "",
                serial: "",
                workType: "",
                workshopName: ""
            },
            layer: false,
            form: {
                appModel: "",
                appSpecifications: "",
                hourQuotaPrice: "",
                id: "",
                idCategory: "",
                pageNum: "",
                pageSize: "",
                postCode: "",
                procedureName: "",
                productName: "",
                serial: "",
                workType: "",
                workshopName: ""
            },
            deptList: [],
            procedureNameList: [],
            loading: false
        };
    },
    mounted() {
        this.getDeptList();
    },
    methods: {
        flush() {
            this.filter = {
                appModel: "",
                appSpecifications: "",
                hourQuotaPrice: "",
                id: "",
                idCategory: "",
                postCode: "",
                procedureName: "",
                productName: "",
                serial: "",
                workType: "",
                workshopName: ""
            };
            this.$refs.list.update(true);
        },
        getDeptList() {
            this.$http
                .get("/haolifa/dept/list")
                .then(res => {
                    res.map(item => {
                        this.deptList.push({
                            text: item.deptName,
                            value: item.deptName
                        });
                    });
                })
                .catch(e => {
                    this.$toast(e.msg || e.message);
                });
        },
        getProcedureList() {
            this.procedureNameList = [];
            this.form.procedureName = this.form.postCode = "";
            this.$http
                .post("/haolifa/pay-working-procedure/getAllList", {
                    id: "",
                    pageNum: "",
                    pageSize: "",
                    postCapability: "",
                    postCode: "",
                    postName: "",
                    productModel: "",
                    serial: "",
                    workType: "",
                    workshopName: this.form.workshopName
                })
                .then(res => {
                    res.map(item => {
                        this.procedureNameList.push({
                            text: item.postName,
                            value: item.postName,
                            postCode: item.postCode
                        });
                    });
                })
                .catch(e => {
                    this.$toast(e.msg || e.message);
                });
        },
        procedureNameChange() {
            this.procedureNameList.map(item => {
                if (this.form.procedureName == item.value) {
                    this.form.postCode = item.postCode;
                }
            });
        },
        edit(item) {
            this.layer = true;
            this.form.id = item.id;
            this.form.appModel = item.appModel;
            this.form.appSpecifications = item.appSpecifications;
            this.form.hourQuotaPrice = item.hourQuotaPrice;
            this.form.idCategory = item.idCategory;
            this.form.postCode = item.postCode;
            this.form.procedureName = item.procedureName;
            this.form.productName = item.productName;
            this.form.serial = item.serial;
            this.form.workType = item.workType;
            this.form.workshopName = item.workshopName;
        },
        remove(item) {
            this.$confirm({
                title: "",
                text: `<br><b>${
                    item.procedureName
                }</b>`,
                color: "red",
                btns: ["", ""],
                yes: () => {
                    this.$http
                        .get(`/haolifa/pay-hour_quota/del/${item.id}`)
                        .then(res => {
                            this.$toast("");
                            this.$refs.list.update(true);
                        })
                        .catch(e => {
                            this.$toast(e.msg || e.message);
                        });
                }
            });
        },
        add() {
            this.layer = true;
        },
        save() {
            if (
                !this.form.workshopName.trim() ||
                !this.form.procedureName.trim()
            ) {
                this.$toast("");
                return;
            }
            this.loading = true;
            let url = this.form.id
                ? "/haolifa/pay-hour_quota/edit"
                : "/haolifa/pay-hour_quota/save";
            this.$http
                .post(url, this.form)
                .then(res => {
                    this.close();
                    this.$toast("");
                    this.$refs.list.update(true);
                    this.loading = false;
                })
                .catch(e => {
                    this.$toast(e.msg || e.message);
                });
        },
        close() {
            this.layer = false;
            this.form = {
                appModel: "",
                appSpecifications: "",
                hourQuotaPrice: "",
                id: "",
                idCategory: "",
                pageNum: "",
                pageSize: "",
                postCode: "",
                procedureName: "",
                productName: "",
                serial: "",
                workType: "",
                workshopName: ""
            };
        }
    }
};
</script>

<style lang="less">
.page-invoice-list {
    select {
        background: none;
        border: none;
        outline: none;
        padding: 5px 20px 5px 10px;
        appearance: none;
    }
    .scroll-y {
        padding-bottom: 40px;
    }

    //
}
.fixed-length {
    width: 100px;
    display: block;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
}
</style>
