<template>
    <div class="page-invoice-list">
        <i class="icon icon-abs" @click="flush">autorenew</i>
        <div class="flex-v-center tool-bar">
            <div class="flex-v-center search-bar" style="margin-right: 20px;">
                <i class="icon f-20 c-8">search</i>
                <input type="text" class="flex-item" v-model="filter.departName" @change="
            $refs.list.update(true)" placeholder="" style="width: 200px;">
                <input type="text" class="flex-item" v-model="filter.postName" @change="
            $refs.list.update(true)" placeholder="" style="width: 200px;">
                <input type="text" class="flex-item" v-model="filter.workType" @change="
            $refs.list.update(true)" placeholder="" style="width: 200px;">
            </div>
            <div class="flex-item"></div>
            <btn class="b" flat color="#008eff" @click="add"></btn>
        </div>
        <div class="flex-item scroll-y">
            <data-list ref="list" :page-size="15" :param="filter" url="/haolifa/pay-production-work-shop/getList" method="post">
                <tr slot="header">
                    <th style="width: 60px;"></th>
                    <th></th>
                    <th></th>
                    <th></th>
                    <th></th>
                    <th></th>
                    <th></th>
                    <th class="t-right" style="width: 80px;"></th>
                </tr>
                <!-- item: ; index:  -->
                <template slot="item" slot-scope="{ item, index }">
                    <td class="c-a">{{ index }}</td>
                    <td>{{ item.departName }}</td>
                    <td>{{ item.postName }}</td>
                    <td>{{ item.workType }}</td>
                    <td>{{ item.postCode }}</td>
                    <td>{{ item.createTime }}</td>
                    <td>{{ item.updateTime }}</td>
                    <td class="t-right">
                        <a href="javascript:;" class="blue" @click="edit(item)"> |&nbsp;</a>
                        <a href="javascript:;" class="red" @click="remove(item)"></a>
                    </td>
                </template>
            </data-list>
        </div>
        <layer v-if="layer" :title="form.id ?'':'' " width="70%">
            <div class="layer-text" style="padding-bottom: 50px;">
                <div class="flex">
                    <input-box v-model="form.departName"  @click="deptFlag=true" class="flex-item mr-20" label=""></input-box>
                    <select-box v-model="form.workType" class="flex-item mr-20" hint="" :list="typeList" label=""></select-box>
                </div>
                <div class="flex">
                    <input-box v-model="form.postName" class="flex-item mr-20" label=""></input-box>
                    <input-box v-model="form.postCode" class="flex-item mr-20" label=""></input-box>
                </div>
            </div>
            <div class="layer-btns">
                <el-button class="mr-20" size="mini" :loading="loading" type="primary" @click="save"></el-button>
                <el-button class="mr-20" size="mini" @click="close"></el-button>
            </div>
        </layer>
        <layer v-if="deptFlag"  width="50%">
            <div class="layer-text" style="padding-bottom: 50px;">
                <dept-select @selectClick="selectClick"></dept-select>
            </div>
        </layer>
    </div>
</template>

<script>
import DataList from "@/components/datalist";
import DeptSelect from "@/components/deptSelect";
export default {
    name: "jiaoxiaogongshi-list",
    components: { DataList ,DeptSelect},
    data() {
        return {
            filter: {
                departName: "",
                endCreateTime: "",
                postName: "",
                startCreateTime: "",
                workType: ""
            },
            layer: false,
            form: {
                id: "",
                postCode: "",
                postName: "",
                workType: "",
                departName: ""
            },
            typeList: [
                { text: "", value: "" },
                { text: "", value: "" }
            ],
            loading: false,
            deptList: [],
            deptFlag:false
        };
    },
    mounted() {
        this.getDeptList();
    },
    methods: {
        selectClick(data){
            if(data){
                this.form.departName = data.name;
            }
            this.deptFlag = false;
        },
        flush() {
            this.filter = {
                departName: "",
                endCreateTime: "",
                postName: "",
                startCreateTime: "",
                workType: ""
            };
            this.$refs.list.update(true);
        },
        edit(item) {
            this.layer = true;
            this.form.id = item.id;
            this.form.postCode = item.postCode;
            this.form.postCapability = item.postCapability;
            this.form.postName = item.postName;
            this.form.productModel = item.productModel;
            this.form.serial = item.serial;
            this.form.workType = item.workType;
            this.form.departName = item.departName;
        },
        getDeptList() {
            this.$http
                .get("/haolifa/dept/list")
                .then(res => {
                    res.map(item => {
                        this.deptList.push({
                            text: item.deptName,
                            value: item.deptName
                        });
                    });
                })
                .catch(e => {
                    this.$toast(e.msg || e.message);
                });
        },
        remove(item) {
            this.$confirm({
                title: "",
                text: `<br><b>${item.postName}</b>`,
                color: "red",
                btns: ["", ""],
                yes: () => {
                    this.$http
                        .get(`/haolifa/pay-production-work-shop/del/${item.id}`)
                        .then(res => {
                            this.$toast("");
                            this.$refs.list.update(true);
                        })
                        .catch(e => {
                            this.$toast(e.msg || e.message);
                        });
                }
            });
        },
        add() {
            this.layer = true;
        },
        save() {
            if (!this.form.postName.trim()) {
                this.$toast("");
                return;
            }
            if (!this.form.postCode.trim()) {
                this.$toast("");
                return;
            }
            if (!this.form.departName.trim()) {
                this.$toast("");
                return;
            }
            if (!this.form.workType.trim()) {
                this.$toast("");
                return;
            }
            this.loading = true;
            let url = this.form.id
                ? "/haolifa/pay-production-work-shop/edit"
                : "/haolifa/pay-production-work-shop/save";
            this.$http
                .post(url, this.form)
                .then(res => {
                    this.close();
                    this.$toast("");
                    this.loading = false;
                    this.$refs.list.update(true);
                })
                .catch(e => {
                    this.$toast(e.msg || e.message);
                });
        },
        close() {
            this.layer = false;
            this.form = {
                id: "",
                postCode: "",
                postName: "",
                workType: "",
                departName: ""
            };
        }
    }
};
</script>

<style lang="less">
.page-invoice-list {
    select {
        background: none;
        border: none;
        outline: none;
        padding: 5px 20px 5px 10px;
        appearance: none;
    }
    .scroll-y {
        padding-bottom: 40px;
    }

    //
}
.fixed-length {
    width: 100px;
    display: block;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
}
</style>
