<template>
    <div class="page-part-list">
        <i class="icon icon-abs" @click="flush">autorenew</i>
        <div class="flex-v-center tool-bar"></div>
        <div class="flex-item scroll-y">
            <data-list ref="list" :param="filter" url="/haolifa/delivery/noticeList" method="post">
                <tr slot="header">
                    <th style="width: 60px;"></th>
                    <th></th>
                    <th style="width:50px;"></th>
                    <th></th>
                    <th></th>
                    <th></th>
                    <th></th>
                    <th></th>
                    <th></th>
                    <th class="t-right" style="width: 80px;"></th>
                </tr>
                <!-- item: ; index:  -->
                <template slot="item" slot-scope="{ item, index }">
                    <td class="c-a">{{index}}</td>
                    <td>{{item.deliveryNo}}</td>
                    <td>
                        <a target="_blank" class="fixed-length" v-if="(item.deliveryUrl).match('\.(pdf|jpe?g|png|bmp)$') " :href="item.deliveryUrl"></a>
                        <a target="_blank" v-if="!(item.deliveryUrl).match('\.(pdf|jpe?g|png|bmp)$')" :href="'http://view.officeapps.live.com/op/view.aspx?src='+ item.deliveryUrl"></a>
                        <!-- <a class="fixed-length" :href="item.deliveryUrl" :title="item.deliveryUrl">{{item.deliveryUrl}}</a> -->
                    </td>
                    <td>{{item.contractOrderNo}}</td>
                    <td>{{auditResultList[item.auditResult]}}</td>
                    <td>{{item.auditInfo}}</td>
                    <td>{{item.auditTime}}</td>
                    <td>{{item.createTime}}</td>
                    <td>{{item.updateTime}}</td>
                    <td class="t-right">
                        <a href="javascript:;" v-if="item.auditResult == 0" class="blue" @click="edit(item)"></a>
                    </td>
                </template>
            </data-list>
        </div>
    </div>
</template>

<script>
import DataList from "@/components/datalist";
export default {
    name: "page-bills-list",
    components: { DataList },
    data() {
        return {
            filter: {},
            auditResultList: ["", "", ""]
        };
    },
    methods: {
        flush() {
            this.$refs.list.update(true);
        },
        edit(item) {
            this.$router.push(`/finance-bills/edit?id=${item.id}`);
        },
        remove(item) {
            this.$confirm({
                title: "",
                text: `<br>${item.deliveryNo}`,
                color: "red",
                btns: ["", ""],
                yes: () => {
                    this.$http
                        .delete(`/haolifa/material/delete/${item.id}`)
                        .then(res => {
                            this.$toast("");
                            this.$refs.list.update(true);
                        })
                        .catch(e => {
                            this.$toast(e.msg);
                        });
                }
            });
        }
    }
};
</script>

<style lang="less">
.page-part-list {
    //
}
.fixed-length {
    width: 100px;
    display: block;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
}
</style>
