<template>
    <div class="page-invoice-list">
        <i class="icon icon-abs" @click="flush">autorenew</i>
        <div class="flex-v-center tool-bar">
            <div class="flex-item"></div>
            <!-- <btn class="b" flat color="#008eff" @click="add"></btn> -->
        </div>
        <div class="flex-item scroll-y">
            <data-list ref="list" :page-size="15" :param="filter" url="/haolifa/pay-assessment-score/getList" method="post">
                <tr slot="header">
                    <th style="width: 60px;"></th>
                    <th></th>
                    <!-- <th></th> -->
                    <th></th>
                    <th class="t-right" style="width: 80px;"></th>
                </tr>
                <!-- item: ; index:  -->
                <template slot="item" slot-scope="{ item, index }">
                    <td class="c-a">{{ index }}</td>
                    <td>{{ item.userName }}</td>
                    <!-- <td>{{ item.assessmentName }}</td> -->
                    <td>{{ item.score }}</td>
                    <td class="t-right">
                        <a href="javascript:;" class="blue" @click="detail(item)"></a>
                    </td>
                </template>
            </data-list>
        </div>
        <layer v-if="layer" title="" width="70%">
            <div class="layer-text" style="padding-bottom: 50px;">
                <el-table :data="tableData" border style="width: 100%">
                    <el-table-column prop="quotaContent" label=""></el-table-column>
                    <el-table-column prop="quotaName" label=""></el-table-column>
                    <el-table-column prop="standard" label=""></el-table-column>
                    <el-table-column prop="score" label=""></el-table-column>
                    <el-table-column prop="remark" label=""></el-table-column>
                    <el-table-column prop="createTime" label=""></el-table-column>
                </el-table>
            </div>
            <div class="layer-btns">
                <el-button class="mr-20" size="mini" @click="layer = false;"></el-button>
            </div>
        </layer>
    </div>
</template>

<script>
import DataList from "@/components/datalist";
export default {
    name: "jiaoxiaodafen-list",
    components: { DataList },
    data() {
        return {
            filter: {},
            layer: false,
            form: {
                id: "",
                assessmentId: "",
                score: "",
                scoreTime: "",
                userId: ""
            },
            loading: false,
            tableData: []
        };
    },
    mounted() {},
    methods: {
        flush() {
            this.$refs.list.update(true);
        },
        edit(item) {
            this.layer = true;
            this.form.id = item.id;
            this.form.assessmentId = item.assessmentId;
            this.form.score = item.score;
            this.form.userId = item.userId;
        },
        remove(item) {
            this.$confirm({
                title: "",
                text: `<br><b>${item.userId}</b>`,
                color: "red",
                btns: ["", ""],
                yes: () => {
                    this.$http
                        .get(`/haolifa/pay-assessment-score/del/${item.id}`)
                        .then(res => {
                            this.$toast("");
                            this.$refs.list.update(true);
                        })
                        .catch(e => {
                            this.$toast(e.msg || e.message);
                        });
                }
            });
        },
        detail(item) {
            this.layer = true;
            this.tableData = [];
            this.$http
                .get("/haolifa/pay-assessment-score/info/" + item.id)
                .then(res => {
                    this.tableData = res;
                })
                .catch(e => {
                    this.$toast(e.msg || e.message);
                });
        },
        save() {
            if (!this.form.postName.trim()) {
                this.$toast("");
                return;
            }
            if (!this.form.postCode.trim()) {
                this.$toast("");
                return;
            }
            if (!this.form.departName.trim()) {
                this.$toast("");
                return;
            }
            if (!this.form.workType.trim()) {
                this.$toast("");
                return;
            }
            this.loading = true;
            let url = this.form.id
                ? "/haolifa/pay-production-work-shop/edit"
                : "/haolifa/pay-production-work-shop/save";
            this.$http
                .post(url, this.form)
                .then(res => {
                    this.close();
                    this.$toast("");
                    this.$refs.list.update(true);
                    this.loading = false;
                })
                .catch(e => {
                    this.loading = false;
                    this.$toast(e.msg || e.message);
                });
        },
        close() {
            this.layer = false;
            this.form = {
                id: "",
                postCode: "",
                postName: "",
                workType: "",
                departName: ""
            };
        }
    }
};
</script>

